<template>
  <div class="body">
    <div class="content">
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">用户名</label>
        <input class="mdui-textfield-input" type="text" v-model="username"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <label class="mdui-textfield-label">密码</label>
        <input class="mdui-textfield-input" type="password" v-model="password"/>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <div class="mdui-btn mdui-ripple mdui-btn-block mdui-color-blue mdui-text-color-white" @click="login">登录</div>
      </div>
      <div class="mdui-textfield mdui-textfield-floating-label">
        <div class="mdui-btn mdui-btn-block mdui-text-color-blue mdui-ripple" style="background-color: white;" @click="register">注册</div>
      </div>
    </div>
  </div>
</template>

<script>
  import Tools from '../sdk/Tools'
  export default {
    name: 'views-login',
    data () {
      return {
        username: null,
        password: null
      }
    },
    mounted () {
      this.$_event.addEvent('onOpened', (res) => {
      })
    },
    methods: {
      login () {
        if (!this.username) {
          this.$_mdui.snackbar({
            message: '请输入用户名',
            position: 'bottom'
          });
          return
        }
        if (!this.password) {
          this.$_mdui.snackbar({
            message: '请输入密码',
            position: 'bottom'
          });
          return
        }
        let _this = this
        var options = {
          apiUrl: WebIM.config.apiURL,
          user: this.username,
          pwd: this.password,
          appKey: WebIM.config.appkey,
          success: function (token) {
            Tools.userSet(token)
            _this.$_mdui.snackbar({
              message: '登录成功',
              position: 'top'
            });
            _this.$router.push('/home')
          }
        };
        this.$_WebIM_conn.open(options);
      },
      register () {
        this.$router.push('/register')
      }
    }
  }
</script>

<style scoped>
.body {
  text-align: center;
  padding: 60px 0 50px 0;
}
  .content {
    display: inline-block;
    width: 230px;
    text-align: left;
  }
</style>



